<template>
  <div id="nav">
    <div id="nav-text" v-on:click="home" title="返回首页">{{webname}}</div>
    <div id="nav-list" v-on:click="list" title="显示列表">列表</div>
    <div id="nav-search">
      <eleselect></eleselect>
    </div>
    <div id="router-choose" v-on:click="ismore($event)">
      <router-link to="/home" id="home" class="router-link">主页&nbsp;</router-link>
      <router-link to="/blog" id="blog" class="router-link">博客</router-link>
    </div>
    <div class="more" v-on:click="more($event)"></div>
  </div>
</template>
<script>
import eleselect from "./elementui/eleselect";
export default {
  name: "mynav",
  data() {
    return {
      webname: "黎生小窝"
    };
  },
  methods: {
    more(e) {
      if (document.getElementById("router-choose").style.right == "10px") {
        document.getElementById("router-choose").style.right = "-80px";
      } else {
        document.getElementById("router-choose").style.right = "10px";
      }
      // 控制菜单状态
    },
    ismore(e){
      if (document.getElementById("router-choose").style.right == "10px") {
        document.getElementById("router-choose").style.right = "-80px";
      }
      // 如果菜单开启则关闭
    },
    home() {
      window.location.href = "http://" + window.location.host;
    },
    // 返回首页的事件
    list() {
      if (document.getElementsByClassName("bloglist")[0].style.left == "0px") {
        document.getElementsByClassName("bloglist")[0].style.left = "-300px";
        document.getElementsByClassName("content")[0].style.color = "#333333";
      } else {
        document.getElementsByClassName("bloglist")[0].style.left = "0px";
        document.getElementsByClassName("content")[0].style.color = "#dddddd";
      }
    },
    // 博客列表的开启与关闭，注意这里的元素初始是none的，切换到移动端时才可以调用
  },
  components: {
    eleselect
  }
};
</script>
<style lang="less">
@main-bg: #388e3c;
@main-color: #4caf50;
#nav {
  display: flex;
  padding: 0 15px;
  background: @main-color;
  justify-content: space-between;
  align-items: center;
  position: relative;
  #nav-text {
    user-select: none;
    font-size: 18px;
    padding: 5px;
    box-shadow: 0 0 2px cornsilk;
    border-radius: 10px;
  }
  a {
    display: inline-block;
    font-weight: bold;
    color: #fff;
    outline: none;
    &.router-link-exact-active {
      box-shadow: 0 0 3px rgb(34, 66, 2);
    }
  }
}
#router-choose {
  a {
    text-decoration: none;
    font-size: 18px;
  }

  #home {
    background: url("../../public/image/home.svg") 0px 2px no-repeat;
    background-size: 25px;
    padding-left: 25px;
  }
  #blog {
    background: url("../../public/image/blog.svg") 0px 1px no-repeat;
    background-size: 28px;
    padding-left: 28px;
  }
  letter-spacing: 5px;
}
.router-link {
  margin: 0 5px;
  padding: 2px;
  border-radius: 5px;
}
.el-input__inner {
  height: 30px !important;
  width: 160px !important;
}
#nav-search {
  margin-right: 30px;
  margin-left: auto;
}
.el-select {
  input:first-child {
    position: relative;
    left: -15px;
    background: #fff url("../../public/image/搜索.png") 0px 0px no-repeat;
    background-size: 30px;
    padding-left: 30px;
  }
}
.more {
  background: url("../../public/image/more.svg") 0px 0px no-repeat;
  background-size: 20px;
  padding-left: 20px;
}
#nav-list {
  display: none;
  user-select: none;
  font-size: 18px;
  padding: 5px;
  box-shadow: 0 0 2px cornsilk;
  border-radius: 10px;
}
@media screen and (max-width: 600px) {
  #router-choose {
    position: fixed;
    border-radius: 5px;
    padding: 5px;
    display: flex;
    flex-direction: column;
    float: right;
    right: -80px;
    top: 70px;
    transition: all 0.5s;
    background: @main-color;
    z-index: 99;
    .router-link {
      margin: 10px 0;
    }
  }
  .more {
    display: block;
    content: "";
    position: absolute;
    right: 20px;
    top: 20px;
    bottom: 0px;
  }
}
</style>