<template>
  <el-select
    @change="search"
    v-model="value"
    filterable
    remote
    reserve-keyword
    placeholder="请输入关键词"
    :remote-method="remoteMethod"
    :loading="loading"
  >
    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
  </el-select>
</template>

<script>
export default {
  data() {
    return {
      options: [],
      value: [],
      list: [],
      loading: false,
      states: []
    };
  },
  mounted() {
    this.$http({
      method: "get",
      url: `http://${window.location.hostname}:9090/api/bloglist`,
      params: {}
    }).then(response => {
      this.states = response.data;
      this.list = this.states.map(item => {
        return { value: `${item}`, label: `${item.split("_")[1]}` };
      });
    });
  },
  methods: {
    remoteMethod(query) {
      this.$http({
        method: "get",
        url: `http://${window.location.hostname}:9090/api/bloglist`,
        params: {}
      }).then(response => {
        this.states = response.data;
      });
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    },
    search(e) {
      var blog = document.getElementById("blog");
      this.$store.commit("selectblog",e);
      blog.click();
    }
  }
};
</script>