<template>
  <div id="app">
    <mynav class="mynav"></mynav>
    <router-view class="router-view"></router-view>
  </div>
</template>

<script>
import mynav from "./components/mynav";
export default {
  name: "app",
  components: {
    mynav
  },
  watch: {
    $route(to, from) {
      if (window.innerWidth <= 600) {
        if (to.path == "/blog") {
          /**
           * $store来自Store对象
           * dispatch 向 actions 发起请求
           */
          document.getElementById("nav-list").style.display = "block";
          document.getElementById("nav-text").style.display = "none";
        } else {
          document.getElementById("nav-list").style.display = "none";
          document.getElementById("nav-text").style.display = "block";
        }
      }
    }
  },
  mounted: function() {
    //这个是钩子函数
    //如果cartView函数要执行，必须先执行钩子函数
    //这个钩子函数完成了对cratView函数的调用
    //应该注意的是，使用mounted 并不能保证钩子函数中的 this.$el 在 document 中。为此还应该引入    Vue.nextTick/vm.$nextTick
    this.$nextTick(function() {
      this.homebloglist();
    });
  },
  methods: {
    homebloglist() {
      this.$http({
        method: "get",
        url: `http://${window.location.hostname}:9090/api/bloglist`,
        params: {}
      }).then(response => {
        this.$store.commit("setData", { key: "bloglist", val: response.data });
      });
    }
  },// 获取博客列表，如果搜索框里有搜索内容则加载后点击选择博客
};
</script>

<style lang="less">
#app {
  display: block;
  height: 100vh;
  position: relative;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100vh;
}
.mynav {
  height: 60px;
}
.router-view {
  position: absolute;
  top: 60px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}
</style>
